	module.exports = {"data":{"markdownRemark":{"html":"<h2>Dark Patterns</h2>\n<p>Dark Patterns are common patterns used to deceive the users of a website or web application.</p>\n<p>Examples include:</p>\n<ul>\n<li>\n<p><a href='https://darkpatterns.org/types-of-dark-pattern/bait-and-switch' target='_blank' rel='nofollow'><strong>Bait and Switch</strong></a> – A user sets out to do one thing, but a different, undesirable thing happens instead.</p>\n</li>\n<li>\n<p><a href=\"https://darkpatterns.org/types-of-dark-pattern/disguised-ads\"><strong>Disguised Ads</strong></a> – Adverts disguised as other kinds of content or navigation, in order to get users to click on them.</p>\n</li>\n<li>\n<p><a href=\"https://darkpatterns.org/types-of-dark-pattern/forced-continuity\"><strong>Forced Continuity</strong></a> – Silently charging a user’s credit card without warning at the end of a free trial.</p>\n</li>\n<li>\n<p><a href=\"https://darkpatterns.org/types-of-dark-pattern/friend-spam\"><strong>Friend Spam</strong></a> – A website or app asks for a user’s email or social media permissions under the pretence it will be used for a desirable outcome (e.g. finding friends), but then spams all the user’s contacts in a message that claims to be from that user.</p>\n</li>\n<li>\n<p><a href=\"https://darkpatterns.org/types-of-dark-pattern/hidden-costs\"><strong>Hidden Costs</strong></a> – At the last step of a checkout process, only to discover some unexpected charges appear, e.g. delivery charges, tax, etc. that were not disclosed prior to processing the user’s payment.</p>\n</li>\n<li>\n<p><a href=\"https://darkpatterns.org/types-of-dark-pattern/misdirection\"><strong>Misdirection</strong></a> – The design purposefully focuses a users’ attention on one thing in order to distract their attention from another.</p>\n</li>\n<li>\n<p><a href=\"https://darkpatterns.org/types-of-dark-pattern/price-comparison-prevention\"><strong>Price Comparison Prevention</strong></a> – An online retailer makes it hard for visitor’s to compare the price of an item with another item, so they cannot make an informed decision.</p>\n</li>\n<li>\n<p><a href=\"https://darkpatterns.org/types-of-dark-pattern/privacy-zuckering\"><strong>Privacy Zuckering</strong></a> – Users are tricked into publicly sharing more information about themselves than they really intended to. Named after Facebook CEO Mark Zuckerberg.</p>\n</li>\n<li>\n<p><a href=\"https://darkpatterns.org/types-of-dark-pattern/roach-motel\"><strong>Roach Motel</strong></a> – The design makes it very easy for users to get into a certain situation, but then makes it hard for them to get out of it (e.g. a subscription).</p>\n</li>\n<li>\n<p><a href=\"https://darkpatterns.org/types-of-dark-pattern/sneak-into-basket\"><strong>Sneak Into Basket</strong></a> – A user attempts to purchase something, but somewhere in the purchasing journey the site sneaks an additional item into their basket, often through the use of an opt-out radio button or checkbox on a prior page.</p>\n</li>\n<li>\n<p><a href=\"https://darkpatterns.org/types-of-dark-pattern/trick-questions\"><strong>Trick Questions</strong></a> – Users are made to respond to a question, which, when glanced upon quickly appears to ask one thing, but if read carefully, asks another thing entirely.</p>\n</li>\n</ul>\n<p>A catalog of Dark Patterns along with a continuously updated list of real-world examples is mainatained at <a href=\"https://darkpatterns.org\">darkpatterns.org</a>.</p>","fields":{"slug":"/developer-ethics/dark-patterns/"},"frontmatter":{"title":"Dark Patterns"}}},"pathContext":{"slug":"/developer-ethics/dark-patterns/"}}

